'use client';

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/Card';
import { DataTable } from '@/components/DataTable';
import { Badge } from '@/components/ui/Badge';
import { mockCourses } from '@/data/mock';

export default function TeacherClasses() {
  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white">My Classes</h1>
        <p className="text-gray-600 dark:text-gray-400 mt-2">
          View all your assigned courses and sections
        </p>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Assigned Courses</CardTitle>
          <CardDescription>All courses you're teaching this semester</CardDescription>
        </CardHeader>
        <CardContent>
          <DataTable
            columns={[
              {
                key: 'code' as any,
                label: 'Course Code',
                render: (code) => String(code),
              },
              {
                key: 'name' as any,
                label: 'Course Name',
                render: (name) => String(name),
              },
              {
                key: 'section' as any,
                label: 'Section',
                render: (section) => <Badge>{String(section)}</Badge>,
              },
              {
                key: 'credits' as any,
                label: 'Credits',
                render: (credits) => String(credits),
              },
              {
                key: 'id' as any,
                label: 'Evaluations',
                render: () => '45',
              },
            ]}
            data={mockCourses.filter(c => c.instructorId === 'user-teacher-1')}
          />
        </CardContent>
      </Card>
    </div>
  );
}
